<template>
	<view class="myname">
		<view class="inputbox" >
			<input type="text" v-model="user.userName" :placeholder="details.placeholder"/>
		</view>
		<view>
			<button type="warn" class="mini-btn" @click="btn">{{details.btn}}</button>
		</view>
	</view>
</template>

<script>
	import json from "../../../static/json/myname.json"
	export default{
		data(){
			return {
				user:'',
				details:""
			}
		},
		onLoad() {
			uni.getStorage({
				key:'lang',
				success:res=>{
					this.details=json[res.data]
					uni.setNavigationBarTitle({
						title:json[res.data].title
					})
				}
			})
			uni.getStorage({
				key:"userInfo",
				success:res=>{
					console.log(res)
					this.user=res.data
					
				}
			})
		},
		methods:{
			btn(){
				uni.showLoading({
					title: '提交中...'
				})
				
				uni.setStorage({
					key:"userInfo",
					data:this.user	,
					success:function () {
						uni.hideLoading();
						uni.showToast({
						title: '提交成功',
						icon :"success",
						duration: 2000
					});
					}
				})
				
			}
		}
	}
</script>

<style>
	.myname{
		width: 100%;
		height: 100vh;
		background: #f4f4f4;
	}
	.inputbox{
		width: 90%;
		margin: auto;
		padding: 5% 0;
		
	}
	.inputbox input{
		background: #FFFFFF;
		height: 2rem;
		border-radius: 5px;
		padding-left: 10px;
	}
	.mini-btn{
		margin: auto;
		width: 90%;
	}
</style>
